<template>
  <div class="temp5">
    <span>冠脉CT检查前，在坚持服用的药物包括： </span><span class="button button-blue" @click="add">添加</span>
    <div class="item flex-row" v-for="(d, index) in drugs">
      <typeahead 
        placeholder="药品名称"
        v-model="d.drugname"
        @change="searchDrug(arguments[0], index)"
        @input="putServer"
        :items="search_item"></typeahead>
      <!--<div style="margin-left: 24px;width: 200px;">共服用<input type="number" style="width: 50px" v-model="d.drug_time" @input="putServer"><span>月， </span></div>-->
      <div class="flex-row">
        <span class="text">共服用</span>
        <input type="number" style="width: 50px" v-model="d.drug_time" @input="putServer" />
        <span class="text">月， 每月 </span>
        <select style="width: 100px" v-model="d.durg_month_time" @change="putServer">
          <option value="超过25天">超过25天</option>
          <option value="15到25天">15到25天</option>
          <option value="小于15天">小于15天</option>
        </select>
        <m-button type="gray" style="margin-left: 24px;" @click="delect(index)">删除</m-button>
      </div>
    </div>
  </div>
</template>
  
<script>
import Typeahead from '../lib/Typeahead.vue'
import MButton from '../lib/Button.vue'
import API from '../api.js'
export default {
  name: 'temp1',
  components: {
    Typeahead,
    MButton
  },
  props: {
    mdid: String,
    templateid: String,
    recordid: String,
    source: {
      type: Object,
      default: {}
    }
  },
  created () {
    if (JSON.stringify(this.source) !== '{}') {
      this.drugs = []
      this.source.map(n => {
        this.drugs.push(n)
      })
    }
  },
  data () {
    return {
      drugs: [
        {drugname: '', drug_time: '', durg_month_time: ''}
      ],
      search_item: []
    }
  },
  methods: {
    searchDrug (query, index) {
      this.drugs[index].drugname = query
      API.SearchDrugs({drugname: query}).then(response => {
        this.search_item = []
        response.map(n => this.search_item.push(n.u_drugname))
      }).catch(err => {
        console.log(err)
      })
    },
    add () {
      this.drugs.push({drugname: '', drug_time: '', durg_month_time: ''})
    },
    putServer () {
      this.$emit('putsever', {sourceData: this.drugs, showData: this.changeData()})
    },
    delect (index) {
      // 删除指定药品
      this.drugs.splice(index, 1)
    },
    // 显示数据
    changeData () {
      let showData = {}
      for (let d in this.drugs) {
        showData[(parseInt(d) + 1) + '. 药品'] = this.drugs[d].drugname + '，共服用 ' + this.drugs[d].drug_time + ' 月，每月' + this.drugs[d].durg_month_time
      }

      return showData
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /*form design*/
  .item {
    padding: 8px 0;
    border-bottom: 1px solid #eee;
  }

  .item span.text {
    line-height: 32px;
    margin: 0 8px;
  }

</style>



